<template>
	<div class="groupChat">
		<similarHeader  @goBack='goBack' :haveMore='false'>
			<h2 v-show='!showSearch'>群聊</h2>
			<van-field v-model="seachValue" placeholder="搜索"  v-show='showSearch' clearable ref='txt' />
			<img src="~img/images/header/search.png" alt="" class='goBack imgRight' @click='search'  v-show='!showSearch'>
			<img src="~img/images/my/add.png" alt="" class='goBack imgRight2' >
		</similarHeader>
		<p class='title'>群聊</p>
		<ul class='groupList'>
			<li>
				<img src="~img/images/groupChat/group1.png" alt="" >
				<div>
				 	海外侨胞
				</div>
			</li>
			<li>
				<img src="~img/images/groupChat/group2.png" alt="" >
				<div>
				 	窗外.不一样的人生
				</div>
			</li>
			<li>
				<img src="~img/images/groupChat/group3.png" alt="" >
				<div>
				 	条条大路通罗马
				</div>
			</li>
			<li>
				<img src="~img/images/groupChat/group4.png" alt="" >
				<div>
				 	走下去，别回头。
				</div>
			</li>
			<li>
				<img src="~img/images/groupChat/group5.png" alt="" >
				<div>
				 	吃得苦中苦方为人上人
				</div>
			</li>
		</ul>
		<p class="count">
			5个群聊
		</p>
	</div>
</template>
<script>
	import  similarHeader from '@/components/similar-header'
	export default{
		name:'groupChat',
		data(){
			return{
				seachValue:'',
				showSearch:false
			}
		},
		methods:{
			search(){
				this.showSearch=true;
				this.$refs.txt.focus();
			},
			goBack(){
				if(this.showSearch){
					this.seachValue='';
					this.showSearch=false;
				}
				else{
					this.$router.go(-1);
				}
			}
		},
		beforeRouteLeave (to, from, next) {
			if(this.showSearch){	
				this.showSearch=false;
				this.seachValue='';		 
				next(false);					
		    }	
		    else{
		    	next();
		    }	
		},
		components:{
			similarHeader
		}
	}
</script>
<style scoped>
	.groupChat{
		height: 100vh;
    	background: #fff;
	}
	header .imgRight{
		position: absolute;
		right: 1.4rem;
	}
	header .imgRight2{
		position: absolute;
		right: .3rem;
	}
	.title{
		background: #ededed;
	    color: #999;
	    font-size: .36rem;
	    padding: .3rem 0 .11rem .38rem;
	}
	.groupList li{
		font-size: .4rem;
	    display: flex;
	    align-items: center;
	    padding: .2rem 0 .2rem .4rem;
	}
	.groupList li div{
		width: 100%;
		position: relative;
	}
	.groupList li div:after{
		content: "";
		position: absolute;
		width: 100%;
		height: 1px;
		background:  rgba(233,233,233,.6);
		bottom:-.44rem;
		left: 0;

	}
	.groupList li img{
		width: .9rem;
	    height: .9rem;
	    margin-right: .3rem;
	}
	.count{
		text-align: center;
	    font-size: .4rem;
	    color: #999;
	    margin-top: .3rem;
	}
	.groupChat >>> .van-cell {
		background: none;
		width: 71%;
	}
	.groupChat >>> .van-field__control {
		font-size: .38rem;
	}
</style>


